import Home from '@/views/Home';
import About from '@/views/About';
import Message from '@/views/Message';
import Blog from '@/views/Blog';
import Project from '@/views/Project';
import BlogDetail from "@/views/Blog/Detail";
export default [
    //  /Home  Foo 
    {
        name: "Home",
        path: '/',
        component: Home,
        meta: {
            title: ''
        }
    },
    //  /About  About 
    {
        name: "About",
        path: '/About',
        component: About,
        meta: {
            title: ''
        }
    },
    //  /Message  Message 
    {
        name: "Message",
        path: '/Message',
        component: Message,
        meta: {
            title: ''
        }
    },
    //  /Blog  Blog 
    {
        name: "Blog",
        path: '/article',
        component: Blog,
        meta: {
            title: ''
        }
    },
    //  /article/cate/:categoryId  Blog 
    {
        name: "CategoryBlog",
        path: '/article/cate/:categoryId',
        component: Blog,
        meta: {
            title: ''
        }
    },
    //  /article/:id  BlogDetail 
    {
        name: "Detail",
        path: '/article/:id',
        component: BlogDetail,
        meta: {
            title: ''
        }
    },
    //  /Project  Project 
    {
        name: "Project",
        path: '/Project',
        component: Project,
        meta: {
            title: '&'
        }
    }
]