<template>
  <div class="blogTOC">
      <h1></h1>
      <RightList @changeSelect="changeSelect" :list="tocWithSelect"/>
  </div>
</template>

<script>
import RightList from "./RightList";
import {debounce} from "@/utils"
export default {
    props: {
        toc:{
            type:Array,
            default:()=>[],
        }
    },
    data(){
        return {
            activeAnchor:"",
        }
    },
    computed: {
        // tocactiveAnchorisSelect
        tocWithSelect(){
            // {anchor: "article-md-title-1" name: ""}
            // 
            const getToc = (toc=[]) => {
                return toc.map((t)=>({
                    ...t,
                    isSelect:t.anchor === this.activeAnchor,
                    // 
                    children:getToc(t.children)
                }))
            }
            return getToc(this.toc);
        },
        // toc
        doms(){
            const doms = [];
            const addDom = toc =>{
                for (const t of toc) {
                    doms.push(document.getElementById(t.anchor))
                    if(t.children && t.children.length){
                        addDom(t.children);
                    }
                }
            }
            addDom(this.toc);
            return doms;
        }
    },
    created(){
        this.newSelect = debounce(this.setActiveAnchor,50)
        this.$bus.$on("mainscroll",this.newSelect)
    },
    // 
    destroyed(){
        this.$bus.$off("mainscroll",this.newSelect)
    },
    methods: {
        changeSelect(item){
            location.hash = item.anchor;
        },
        /**
         * ActiveAnchor
         */
        setActiveAnchor(scrollDom){
            if(!scrollDom){
                return;
            }
            this.activeAnchor = "";// 
            // 
            const range = 200;
            for (const dom of this.doms){
                // dom
                if(!dom){
                    continue; // 
                }
                // getBoundingClientRect 
                const top = dom.getBoundingClientRect().top;// 
                if(top >= 0 && top<=200){
                    // 
                    this.activeAnchor = dom.id;
                    return;
                }else if(top > range){
                    // 
                    return; // 
                }else{
                    // 
                    this.activeAnchor = dom.id; // 
                }
            }
        }
    },
    components: {
        RightList,
    }
}
</script>

