import axios from "axios";
import showMessage from "@/utils/showMessage";
const ins = axios.create(); // axios
//  returnaxios
ins.interceptors.response.use(function(response) {
    if (response.data.code !== 0) {
        // 
        showMessage({
            content: "",
            duration: 2000,
            type: 'error'
        });
        return null;
    }
    return response.data.data
});


export default ins;